.form-addon {
  margin: 0 0 15px;
  padding: 0;

  border: none;
}

.form-addon__title {
  position: relative;

  margin-bottom: 40px;
  padding: 0;

  &::after {
    content: "";
    position: absolute;
    top: 53%;
    left: 116%;

    width: 136px;
    height: 3px;

    background-color: $green;
  }
}

.form-addon__grid {
  display: flex;
  flex-direction: column;
}

.form-addon__grid-element {
  display: flex;
}

.form-addon__label {
  position: relative;

  margin-bottom: 25px;
  margin-left: 46px;

  cursor: pointer;

  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: -6px;
    left: -46px;

    width: 29px;
    height: 29px;

    border: 1px solid $gray--form-border;
  }

  &:hover::before {
    border-color: #bababa;
  }

  &:active {
    opacity: 0.3;
  }
}

.form-addon__input {
  display: none;

  &:checked + .form-addon__label::after {
    content: "";
    position: absolute;
    top: -2px;
    left: -41px;

    width: 19px;
    height: 11px;

    border-bottom: 3px solid $green;
    border-left: 3px solid $green;
    transform: rotate(-45deg);
  }
}

@media (min-width: $tablet-width) {
  .form-addon {
    margin-bottom: 13px;
  }

  .form-addon__title {
    margin-bottom: 67px;

    &::before {
      content: "";
      position: absolute;
      top: 23px;
      left: -673px;

      width: 300%;
      height: 3px;

      background-color: $green;
    }

    &::after {
      top: 23px;
      right: -520%;

      width: 500%;
    }
  }

  .form-addon__grid {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }

  .form-addon__grid-element {
    &:nth-child(odd) {
      width: 54.5%;
    }

    &:nth-child(even) {
      width: 45.5%;
    }
  }

  .form-addon__label {
    margin-bottom: 52px;
    margin-left: 59px;

    &::before {
      content: "";
      position: absolute;
      top: -9px;
      left: -58px;

      width: 39px;
      height: 39px;

      border: 1px solid $gray--form-border;
    }
  }

  .form-addon__input:checked + .form-addon__label::after {
    top: 0;
    left: -49px;
  }
}

@media (min-width: $desktop-width) {
  .form-addon {
    margin-bottom: 19px;
  }

  .form-addon__title {
    &::before {
      content: "";
      position: absolute;
      top: 23px;
      left: -674px;

      width: 300%;
      height: 3px;

      background-color: $green;
    }

    &::after {
      top: 23px;
      right: -1119px;

      width: 800%;
    }
  }

  .form-addon__grid-element {
    &:nth-child(odd) {
      width: 20%;
    }

    &:nth-child(even) {
      width: 20%;
    }
  }
}
